/* ==========================================================================
   Vertical Split Slider shortcode style - begin
   ========================================================================== */

.qodef-vss-initialized {
	
	.qodef-content {
		
		.qodef-content-inner {
			
			> .qodef-container {
				
				> .qodef-container-inner {
					padding: 0;
				}
			}
			
			> .qodef-full-width {
				
				> .qodef-full-width-inner {
					padding: 0;
				}
			}
		}
	}
}

.qodef-vertical-split-slider {
	position: relative;
	opacity: 0;
	margin: 0 -2px;
	overflow: hidden;
	
	.qodef-vss-ms-left,
	.qodef-vss-ms-right {
		position: absolute;
		width: 50%;
		height: 100%;
		box-sizing: border-box;
	}

	.qodef-vss-ms-left {
		top: 0;
		left: 0;
		
		.qodef-vss-ms-section {
			background-position: 100% center;
			background-size: cover;
		}
	}

	.qodef-vss-ms-right {
		top: 10000px;
		right: 0;
		
		.qodef-vss-ms-section {
			background-position: 0 center;
			background-size: cover;
		}
	}
	
	.qodef-vss-ms-section {
		
		&.ms-table{
			display: table;
			width: 100%;
			table-layout: fixed;
			box-sizing: border-box;
		}
		
		.ms-tableCell {
			display: table-cell;
			vertical-align: middle;
			width: 100%;
			height: 100%;
			box-sizing: border-box;
			@include qodefTransition(padding .45s cubic-bezier(.4, 0, .2, 1));
		}
		
		.ms-easing {
			@include qodefTransition(all 0.7s ease-out);
		}
	}
	
	&.qodef-vss-scrolled {
		
		.qodef-vss-ms-left {
			
			.qodef-vss-ms-section {
				
				.ms-tableCell {
					padding: 7.5rem 0 7.5rem 7.5rem;
				}
			}
		}
		
		.qodef-vss-ms-right {
			
			.qodef-vss-ms-section {
				
				.ms-tableCell {
					padding: 7.5rem 7.5rem 7.5rem 0;
				}
			}
		}
		
		.qodef-vss-horizontal-mask {
			
			&:before,
			&:after {
				@include qodefTransform(scaleY(1));
			}
		}
		
		.qodef-vss-vertical-mask {
			
			&:before,
			&:after {
				@include qodefTransform(scaleX(1));
			}
		}
	}
	
	.qodef-vss-horizontal-mask {
		
		&:before,
		&:after {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			background-color: #fff;
			height: 7.5rem;
			z-index: 500;
			@include qodefTransform(scaleY(0));
			@include qodefTransitionTransform(.45s cubic-bezier(.4, 0, .2, 1));
		}
		
		&:before {
			top: 0;
			-webkit-transform-origin: center top;
			-ms-transform-origin: center top;
			transform-origin: center top;
		}
		
		&:after {
			bottom: 0;
			-webkit-transform-origin: center bottom;
			-ms-transform-origin: center bottom;
			transform-origin: center bottom;
		}
	}
	
	.qodef-vss-vertical-mask {
		
		&:before,
		&:after {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			background-color: #fff;
			width: 7.5rem;
			z-index: 500;
			@include qodefTransform(scaleX(0));
			@include qodefTransitionTransform(.45s cubic-bezier(.4, 0, .2, 1));
		}
		
		&:before {
			left: 0;
			-webkit-transform-origin: center left;
			-ms-transform-origin: center left;
			transform-origin: center left;
		}
		
		&:after {
			right: 0;
			-webkit-transform-origin: center right;
			-ms-transform-origin: center right;
			transform-origin: center right;
		}
	}
}

.qodef-vss-responsive {
	display: none;
	
	.qodef-vss-ms-section {
		background-position: center;
		background-size: cover;
	}
}

#multiscroll-nav {
	position: fixed;
	top: 50%;
	right: 55px;
	margin-top: -10px;
	opacity: 1;
	z-index: 1000;
	@include qodefTransition(opacity .5s ease-out);
	
	.qodef-fullscreen-menu-opened & {
		z-index: -1;
	}

	ul {
		margin: 0;
		padding: 0;

		li {
			position: relative;
			display: block;
			margin: 12px 3px;

			a {
				position: relative;
				display: block;
				width: 14px;
				height: 14px;
				margin: 0 auto;
				background-color: transparent;
				z-index: 1;
				box-sizing: border-box;
				
				&:before,
				&:after {
					position: absolute;
					content: '';
				}
				
				&:before {
					top: -1px;
					left: -1px;
					width: 100%;
					height: 100%;
					border: 1px solid #313131;
					opacity: 0;
					@include qodefTransition(opacity .5s ease);
				}
				
				&:after {
					top: 50%;
					left: 50%;
					width: 5px;
					height: 5px;
					background-color: #313131;
					@include qodefTransform(translate(-50%,-50%));
				}
				
				&.active,
				&:hover {
					
					&:before {
						opacity: 1;
					}
				}

				&.active {

					&:before {
						border-color: #d7d7d7 !important;
					}

					&:after {
						background-color: #d7d7d7 !important;
					}
				}
				
				.qodef-custom-light-header & {
					&:before {
						border-color: $header-light-color;
					}
					
					&:after {
						background-color: $header-light-color;
					}
				}
				
				.qodef-custom-dark-header & {
					&:before {
						border-color: $default-heading-color;
					}
					
					&:after {
						background-color: $default-heading-color;
					}
				}
				
				span {
					display: block;
				}
			}
		}
	}
}
/* ==========================================================================
   Vertical Split Slider shortcode style - end
   ========================================================================== */